

import { Component, OnDestroy, OnInit } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';

import { Device } from '@capacitor/device';
import { Subscription } from 'rxjs';
import { Dropdown } from 'primeng/dropdown';

import { PatrimonioService } from '../../../../../shared/services/patrimonio/patrimonio.service';
import { CondominioService } from '../../../../services/condominio/condominio.service';
import { CondominioDto } from '../../../../interfaces/condominios/condominio.dto';
import { PageableDto } from './../../../../interfaces/others/pageable.dto';
import { PageOrder } from './../../../../constants/page.constant';
import { condominio_reserva_arvores4 } from './../../../../constants/system.constant';

@Component({
  selector: 'app-patrimonio-cadastro',
  templateUrl: './patrimonio-cadastro.component.html',
  styleUrls: ['./patrimonio-cadastro.component.scss'],
})
export class PatrimonioCadastroComponent implements OnInit, OnDestroy {

  form: UntypedFormGroup;
  condominios: PageableDto<CondominioDto>;
  selectedCondominio: string = condominio_reserva_arvores4;
  currentCondominiosPage = 1;
  searchedCondominio = '';

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: UntypedFormBuilder,
    private patrimonioService: PatrimonioService,
    private condominioService: CondominioService,
    private modal: ModalController,
  ) {
    this.form = this.formBuilder.group({
      nome: [, [Validators.required]],
      descricao: [, [Validators.required]],
      valor_patrimonio: [, [Validators.required,]],
      condominio_id: [condominio_reserva_arvores4, [Validators.required,]],
      id_fisico: [],
    });
  }

  ngOnInit() {
    // this.loadCondominios();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }


  loadCondominios(page = 1, dropdown?: Dropdown) {
    const sub = this.condominioService.list(
      { searchedCondominio: this.searchedCondominio },
      { order: PageOrder.ASC, page, take: 10 })
      .subscribe(data => {
        this.condominios = data;
        if (dropdown) {
          dropdown.show();
        }
      });

    this.subscriptions.add(sub);
  }
  searchCondominio(dropdown?: Dropdown) {
    this.loadCondominios(1, dropdown);
  }
  paginarCondominios(event, dropdown?: Dropdown) {
    const { first, rows } = event;
    const page = Number((Number(first) / Number(rows)) + 1);
    if (page != this.currentCondominiosPage) {
      this.currentCondominiosPage = page;
      this.loadCondominios(page, dropdown);
    }
  }

  async create() {
    const nome = this.form.get('nome').value;
    const descricao = this.form.get('descricao').value;
    const valor_patrimonio = this.form.get('valor_patrimonio').value;
    const id_fisico = this.form.get('id_fisico').value;
    const condominio_id = this.form.get('condominio_id').value;

    let idOfThisPlataform = undefined;
    if (id_fisico) {
      idOfThisPlataform = (await Device.getId()).uuid;
    }

    const sub = this.patrimonioService.create(
      { nome, descricao, valor_patrimonio, id_fisico: idOfThisPlataform, condominio_id }
    )
      .subscribe(response => {
        this.fechar(true, { condominio_id });
      });

    this.subscriptions.add(sub);
  }

  fechar(wasCreated = false, data?: any) {
    this.modal.dismiss(wasCreated, data);
  }

  isInputError(inputName: string): boolean {
    const resp =
      !this.form.controls[inputName].untouched &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableCreateBtn(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
